import React, { Component } from 'react';

import {Grid, Divider, Typography, Collapse, ListItem, ListItemText, Chip} from '@material-ui/core';

// import Colors from '../Colors'
import data from '../JoseVelarde.json';



class RandomColorChip extends Component {
	constructor(props) {
		super(props);
		this.state = {
			bgColor: "default"
		}
		this.colors = ["default", "primary", "secondary", "error", "warning", "info", "success"]
	}

	changeColors = () =>{
		let index = Math.floor(Math.random() * (6))
		while (this.colors[index] === this.state.bgColor){
			index = Math.floor(Math.random() * (6))
		}
		
		this.setState((prevState) =>({
			bgColor: this.colors[index]
		}));	
	}

	render() { 
		return (
				<>
					<Chip {...this.props} onClick={this.changeColors} color={this.state.bgColor}/>
				</>
		);
	}
}

class SideInterests extends Component {
	constructor(props) {
		super(props);
		this.state = {
			open: true,
		}
	}
	

	handleClick = () =>{
		this.setState(prevState=>({
			open: !prevState.open
		}));
	}
	updateDimensions = () => {
		if(window.innerWidth < 900){
			this.setState({ open: false });
		} else {
			this.setState({ open: true });
		}
	}
	componentDidMount() {
		this.updateDimensions()
		window.addEventListener('resize', this.updateDimensions);
	}
	componentWillUnmount() {
		window.removeEventListener('resize', this.updateDimensions);
	}
	render() { 
		return (
			<Grid container rowSpacing={1.5} pb={2}>
				<Grid item xs={12} md={12} lg={12}>
					<ListItem button onClick={this.handleClick}>
						<ListItemText>
							<Divider variant="fullWidth">
								<Typography variant="h5" >
									Interests
								</Typography>
							</Divider>
						</ListItemText>
					</ListItem>
				</Grid>
				<Collapse in={this.state.open} timeout="auto" unmountOnExit sx={{width: '100%'}} >
					{data.Filler.Interests.map((interest, index) =>
							<RandomColorChip key={ "interest" + index} label={interest} sx={{typography: 'body1', margin: '3px'}}/>
					)}
				</Collapse>
			</Grid>
		);
	}
}

export default SideInterests;
