import React, { Component } from 'react';

import {Grid, Typography, Divider, Chip, Collapse, ListItem, ListItemText} from '@material-ui/core';

import data from '../JoseVelarde.json'

class Experience extends Component {
	constructor(props) {
		super(props);
		this.state = {
			open: true
		}
	}
	handleClick = () =>{
		this.setState(prevState=>({
			open: !prevState.open
		}));
	}
	updateDimensions = () => {
		if(window.innerWidth < 900){
			this.setState({ open: false });
		} else {
			this.setState({ open: true });
		}
	}
	componentDidMount() {
		this.updateDimensions()
		window.addEventListener('resize', this.updateDimensions);
	}
	componentWillUnmount() {
		window.removeEventListener('resize', this.updateDimensions);
	}


	render() { 
		return (
			<Grid container rowSpacing={1.5}>
				<Grid item xs={12} md={12} lg={12}>
					<ListItem button onClick={this.handleClick}>
						<ListItemText>
							<Divider variant="fullWidth">
								<Typography variant="h5" >
									Experience
								</Typography>
							</Divider>
						</ListItemText>
					</ListItem>
				</Grid>
				<Collapse in={this.state.open} timeout="auto" unmountOnExit sx={{width: '100%'}}>
					{data.Experience.map((exp, index) =>
						<Grid item container key={ "exp" + index} rowSpacing={0.6}>
							<Grid item container sx={{display :'flex', justifyContent: 'space-between'}}>
								<Grid item xs={5} md={5} lg={8} sx={{textAlign: 'start', alignSelf: 'center'}}>
									<Typography variant="h6" fontWeight="500">{exp["Job Title"]}</Typography>
								</Grid>
								<Grid item xs={7} md={7} lg={4} sx={{textAlign: 'end', alignSelf: 'center'}}>
									<Typography variant="subtitle1">{exp["Date"]}</Typography>
								</Grid>
							</Grid>
							<Grid item container sx={{display :'flex', justifyContent: 'space-between'}} >
								<Grid item xs={5} md={5} lg={8} sx={{textAlign: 'start', alignSelf: 'center'}}>
									<Typography variant="body1" color="text.secondary" >{exp["Organization"]}</Typography>
								</Grid>
								<Grid item xs={7} md={7} lg={4} sx={{textAlign: 'end', alignSelf: 'center'}}>
									<Typography variant="subtitle1" color="text.terciary">{exp["Location"]}</Typography>
								</Grid>
							</Grid>
							<Grid item xs={12} md={12} lg={88}>
								<Divider textAlign="left">
									<Chip label="Achievements" />
								</Divider>
							</Grid>
							{exp["Tasks"].map((task, index)=>
								<Grid item xs={12} md={12} lg={12}>
									<Typography variant="body1" dangerouslySetInnerHTML={{__html: task}}></Typography>
								</Grid>
							)}
							<Grid item xs={12} md={12} lg={12} mt={1}>
								{index === (data.Experience.length - 1)?
									null
									:
									<Divider variant="middle"/>
								}
							</Grid>
						</Grid>
					)}
				</Collapse>
			</Grid>
		);
	}
}

export default Experience;